<template>
  <div id="app" class="app">
    <!-- Á∂≤Á´ô Header -->
    <header class="site-header">
      <h2>‰∏ÄÁï™Ë≥ûÊäΩÁçéÁ∂≤Á´ôÔºàÊâìÁâàÁâàÔºâ</h2>
    </header>

    <main class="main">
      <section v-if="loading" class="state">
        ËÆÄÂèñ‰∏≠...
      </section>

      <section v-else-if="error" class="state error">
        ËºâÂÖ•Â§±ÊïóÔºö{{ error }}
      </section>

      <section v-else class="content-layout">
        <!-- Â∑¶ÂÅ¥Ôºö‰∏ÄÁï™Ë≥ûÂêçÁ®±„ÄÅÁ∞°‰ªã„ÄÅÊäΩÁçé„ÄÅÂúñÁâáÁÄèË¶Ω -->
        <!-- Â∑¶ÂÅ¥ÔºöÁÄèË¶ΩÂúñÁâá ‚Üí ‰∏ÄÁï™Ë≥ûÂêçÁ®±/Á∞°‰ªã ‚Üí ÊäΩÁçé -->
      <section class="left-panel">
        <!-- ÁÄèË¶ΩÂúñÁâáÔºöbanner + ÂÖ®ÈÉ®ÁçéÂìÅ -->
        <div class="preview-block">
          <div class="preview-box">
            <template v-if="currentSlide">
              <h4 class="preview-name">
                {{ currentSlide.title }}
              </h4>
              <img
                class="preview-img"
                :src="currentSlide.imageUrl"
                :alt="currentSlide.title"
              />
              <p class="preview-desc">
                {{ currentSlide.description }}
              </p>
            </template>
            <template v-else>
              <p class="placeholder">
                Ë≥áÊñôËºâÂÖ•ÊúâË™§ÔºåË´ãÁ®çÂæåÂÜçË©¶
              </p>
            </template>
          </div>

          <!-- Â∑¶Âè≥ÂàáÊèõ -->
          <div class="preview-nav" v-if="slides.length > 1">
            <button class="nav-btn" @click="prevSlide">‰∏ä‰∏ÄÂºµ</button>
            <span class="nav-info">
              {{ currentSlideIndex + 1 }} / {{ slides.length }}
            </span>
            <button class="nav-btn" @click="nextSlide">‰∏ã‰∏ÄÂºµ</button>
          </div>
        </div>

        <!-- ‰∏ÄÁï™Ë≥û infoÔºöÂêçÁ®± + ÊäΩÁçéÊåâÈàïÔºàÂè≥ÂÅ¥Ôºâ -->
        <div class="series-info-block" v-if="series">
          <div class="series-header">
            <h1 class="series-title">{{ series.name }}</h1>

            <!-- ÊåâÈàïÁßªÂà∞Âè≥ÈÇä -->
            <button
              class="draw-btn header-draw-btn"
              @click="drawOnce"
              :disabled="!box || box.remainingTickets <= 0"
            >
              ÊäΩÁçé
            </button>
          </div>

          <p class="series-meta">
            ÂñÆÊäΩÔºö{{ series.pricePerDraw }} ÂÖÉ ÔΩú Êú¨ÁõíÁ∏ΩÊäΩÊï∏Ôºö
            {{ box ? box.totalTickets : 0 }} ÊäΩ ÔΩú Ââ©È§òÔºö
            {{ box ? box.remainingTickets : 0 }} ÊäΩ
          </p>

          <p class="series-desc" v-if="series.description">
            {{ series.description }}
          </p>
        </div>
      </section>


        <!-- Âè≥ÂÅ¥ÔºöÁçéÈ†ÖÊ∏ÖÂñÆ -->
        <section class="right-panel">
          <h3>ÁçéÈ†ÖÊ∏ÖÂñÆ</h3>
          <ul class="prize-list">
            <li
              v-for="p in prizeTiers"
              :key="p.id"
              class="prize-item"
              :class="{
                selected: selectedPrize && selectedPrize.id === p.id,
                soldout: isPrizeSoldOut(p)
              }"
              @click="selectPrize(p)"
            >
              <div class="prize-line">
                <strong class="tier-bold">{{ p.tierCode }}Ë≥û</strong>
                <span class="prize-text">
                   {{ p.name }}
                </span>
              </div>

              <!-- ÊúÄÂæåË≥ûÈ°ØÁ§∫ÊñáÂ≠ó -->
              <div class="prize-status" v-if="p.isLastPrize">
                <span>
                  {{ lastPrizeClaimed ? "Â∑≤Áç≤Âæó" : "Â∞öÊú™ÈñãÂá∫" }}
                </span>
              </div>

              <!-- ‰∏ÄËà¨Ë≥ûÈ°ØÁ§∫ Ââ©È§ò / Á∏ΩÊï∏ -->
              <div class="prize-status" v-else>
                <span>
                  Ââ©È§ò / Á∏ΩÊï∏Ôºö
                  {{ getRemainingQty(p.id) }} / {{ getTotalQty(p.id) }}
                </span>
              </div>
            </li>
          </ul>
        </section>
      </section>
    </main>

    <!-- ÊäΩÁçéÁµêÊûúÂ∞èË¶ñÁ™ó -->
    <div
      v-if="showResultModal && currentPrize"
      class="modal-backdrop"
      @click.self="closeModal"
    >
      <div class="modal">
        <button class="modal-close" @click="closeModal">
          √ó
        </button>
        <h3 class="modal-title">Êú¨Ê¨°ÊäΩÁçéÁµêÊûú</h3>
        <p class="modal-prize-tier">
          {{ currentPrize.tierCode }}Ë≥û
        </p>
        <p class="modal-prize-name">
          {{ currentPrize.name }}
        </p>
        <img
          class="modal-img"
          :src="currentPrize.imageUrl"
          :alt="currentPrize.name"
        />
        <p class="modal-desc">
          {{ currentPrize.description }}
        </p>
        <p class="modal-remain" v-if="box">
          Êú¨ÁõíÂâ©È§òÊäΩÊï∏Ôºö{{ box.remainingTickets }}
        </p>
        <button class="modal-ok" @click="closeModal">
          Á¢∫Ë™ç
        </button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      loading: true,
      error: null,
      series: null,
      prizeTiers: [],
      box: null,

      // ÁÄèË¶ΩÂçÄ
      slides: [], // Á¨¨‰∏ÄÂÄãÊòØ bannerÔºåÂÖ∂È§òÊòØÂêÑË≥ûÂúñÁâá
      currentSlideIndex: 0,

      // ÁãÄÊÖã
      selectedPrize: null,
      currentPrize: null,
      lastPrizeClaimed: false,
      showResultModal: false
    };
  },
  computed: {
    currentSlide() {
      if (!this.slides || this.slides.length === 0) {
        return null;
      }
      return this.slides[this.currentSlideIndex];
    }
  },
  async created() {
    try {
      const res = await fetch(process.env.BASE_URL + "data/gacha.json");
      if (!res.ok) {
        throw new Error("HTTP " + res.status);
      }
      const data = await res.json();

      // Á≥ªÂàóË≥áÊñô
      this.series = {
        id: data.series.id,
        name: data.series.name,
        pricePerDraw: data.series.pricePerDraw,
        description: data.series.description,
        bannerImage: data.series.bannerImage
      };

      // ÁçéÈ†ÖË≥áÊñôÔºöÂä†‰∏ä isLastPrize
      this.prizeTiers = data.prizeTiers.map(function (p) {
        return {
          id: p.id,
          tierCode: p.tierCode,
          name: p.name,
          imageUrl: p.imageUrl,
          description: p.description,
          isLastPrize:
            p.id === 0 ||
            p.tierCode === "ÊúÄÂæåË≥û" ||
            (typeof p.tierCode === "string" &&
              p.tierCode.indexOf("ÊúÄÂæå") !== -1)
        };
      });

      // ÁõíÂ≠êË≥áÊñô
      this.box = data.box;

      // ÊßãÂª∫ÁÄèË¶ΩÁî® slidesÔºöÂÖàÊîæ bannerÔºåÂÜç‰æùÁÖß prizeTiers È†ÜÂ∫èÊîæÁçéÂìÅ
      var slides = [];

      if (this.series.bannerImage) {
        slides.push({
          id: "banner",
          type: "banner",
          title: this.series.name + " ‰∏ªË¶ñË¶∫",
          imageUrl: this.series.bannerImage,
          description: this.series.description,
          prizeId: null
        });
      }

      for (var i = 0; i < this.prizeTiers.length; i++) {
        var p = this.prizeTiers[i];
        slides.push({
          id: "prize-" + p.id,
          type: "prize",
          title: p.tierCode + "Ë≥û " + p.name,
          imageUrl: p.imageUrl,
          description: p.description,
          prizeId: p.id
        });
      }

      this.slides = slides;
      this.currentSlideIndex = 0;
      this.updateSelectedFromSlide();
    } catch (err) {
      this.error = err.message || String(err);
    } finally {
      this.loading = false;
    }
  },
  methods: {
    // ÂæûÁõÆÂâç slide ÂêåÊ≠• selectedPrizeÔºàÂè™ÊúâÁï∂ slide ÊòØ prize ÊôÇÔºâ
    updateSelectedFromSlide() {
      var slide = this.currentSlide;
      if (!slide || slide.type !== "prize") {
        this.selectedPrize = null;
        return;
      }
      var prize = this.prizeTiers.find(function (p) {
        return p.id === slide.prizeId;
      });
      this.selectedPrize = prize || null;
    },

    // ÁÄèË¶ΩÂçÄÔºö‰∏ä‰∏ÄÂºµ
    prevSlide() {
      if (!this.slides || this.slides.length === 0) {
        return;
      }
      if (this.currentSlideIndex === 0) {
        this.currentSlideIndex = this.slides.length - 1;
      } else {
        this.currentSlideIndex = this.currentSlideIndex - 1;
      }
      this.updateSelectedFromSlide();
    },

    // ÁÄèË¶ΩÂçÄÔºö‰∏ã‰∏ÄÂºµ
    nextSlide() {
      if (!this.slides || this.slides.length === 0) {
        return;
      }
      if (this.currentSlideIndex === this.slides.length - 1) {
        this.currentSlideIndex = 0;
      } else {
        this.currentSlideIndex = this.currentSlideIndex + 1;
      }
      this.updateSelectedFromSlide();
    },

    // ÈªûÊ∏ÖÂñÆ ‚Üí ÁÄèË¶ΩÂçÄË∑≥Âà∞Ë©≤ÁçéÈ†ÖÂúñÁâá
    selectPrize(prize) {
      this.selectedPrize = prize;
      if (!this.slides || this.slides.length === 0) {
        return;
      }
      var index = this.slides.findIndex(function (s) {
        return s.type === "prize" && s.prizeId === prize.id;
      });
      if (index >= 0) {
        this.currentSlideIndex = index;
      }
    },

    // ÂèñÂæóÊüêÂÄãË≥ûÁ∏ΩÊï∏
    getTotalQty(prizeId) {
      if (!this.box || !this.box.prizeStocks) {
        return 0;
      }
      var stock = this.box.prizeStocks.find(function (s) {
        return s.prizeTierId === prizeId;
      });
      return stock ? stock.initialQty : 0;
    },

    // ÂèñÂæóÊüêÂÄãË≥ûÂâ©È§òÊï∏
    getRemainingQty(prizeId) {
      if (!this.box || !this.box.prizeStocks) {
        return 0;
      }
      var stock = this.box.prizeStocks.find(function (s) {
        return s.prizeTierId === prizeId;
      });
      return stock ? stock.remainingQty : 0;
    },

    // ÊòØÂê¶ÊäΩÂÆå
    isPrizeSoldOut(prize) {
      if (prize.isLastPrize) {
        return this.lastPrizeClaimed;
      }
      return this.getRemainingQty(prize.id) === 0;
    },

    // ÈóúÈñâÊäΩÁçéÁµêÊûúË¶ñÁ™ó
    closeModal() {
      this.showResultModal = false;
    },

    // ÊäΩÁçéÔºöÊúÄÂæåË≥ûÂè™ÊúÉÂú®ÊúÄÂæå‰∏ÄÊäΩÂá∫Áèæ
    // ÊäΩÁçéÔºö‰ΩøÁî®„ÄåÂä†Ê¨äÈö®Ê©ü„ÄçÔºå‰æùÁÖßÂâ©È§òÊï∏ÈáèÊ±∫ÂÆöÊ©üÁéá
drawOnce() {
  if (!this.box) {
    return;
  }

  // Ê≤íÊäΩÊï∏‰∫Ü
  if (this.box.remainingTickets <= 0) {
    alert("ÈÄô‰∏ÄÁõíÂ∑≤Á∂ìÊäΩÂÆå‰∫Ü");
    return;
  }

  // ‚úÖ Ââ©‰∏ã‰∏ÄÊäΩÔºö‰∏ÄÂÆöÁµ¶ÊúÄÂæåË≥û
  if (this.box.remainingTickets === 1) {
    var lastPrize = this.prizeTiers.find(function (p) {
      return p.isLastPrize;
    });
    if (!lastPrize) {
      alert("Êâæ‰∏çÂà∞ÊúÄÂæåË≥ûË≥áÊñô");
      return;
    }

    this.currentPrize = lastPrize;
    this.lastPrizeClaimed = true;
    this.box.remainingTickets = this.box.remainingTickets - 1;
    this.showResultModal = true;

    // ÁÄèË¶ΩÂçÄËàáÊ∏ÖÂñÆÈÉΩË∑≥Âà∞ÊúÄÂæåË≥û
    this.selectPrize(lastPrize);
    return;
  }

  // üßÆ ‰∏ÄËà¨ÊäΩÁçéÔºöÁî®„ÄåÂâ©È§òÊï∏Èáè„ÄçÂä†Ê¨äÈö®Ê©ü
  if (!this.box.prizeStocks || this.box.prizeStocks.length === 0) {
    alert("Êú™Ë®≠ÂÆöÁçéÈ†ÖÂ∫´Â≠ò");
    return;
  }

  // 1) ÂÖàÁÆóÊâÄÊúâÁçéÈ†ÖÂâ©È§òÁ∏ΩÊï∏Ôºà‰∏çÂê´ÊúÄÂæåË≥ûÔºåÂõ†ÁÇ∫ÊúÄÂæåË≥ûÈÄöÂ∏∏‰∏çÂú® prizeStocks Ë£°Ôºâ
  var totalRemaining = 0;
  for (var i = 0; i < this.box.prizeStocks.length; i++) {
    var qty = this.box.prizeStocks[i].remainingQty;
    if (qty > 0) {
      totalRemaining += qty;
    }
  }

  // Â¶ÇÊûúÁ∏ΩÊï∏ <= 0Ôºå‰ª£Ë°®‰∏ÄËà¨Ë≥ûÈÉΩÊäΩÂÆå‰∫ÜÔºåÊáâË©≤Âè™Ââ©ÊúÄÂæåË≥ûÈöéÊÆµ
  if (totalRemaining <= 0) {
    alert("‰∏ÄËà¨Ë≥ûÂ∫´Â≠òÁÇ∫ 0ÔºåÂ∞áÈÄ≤ÂÖ•ÊúÄÂæåË≥ûÈöéÊÆµ");
    this.box.remainingTickets = 1;
    return;
  }

  // 2) Âú® 1 ~ totalRemaining ‰πãÈñìÊäΩ‰∏ÄÂÄãËôüÁ¢º
  var ticketNo = Math.floor(Math.random() * totalRemaining) + 1;

  // 3) Áî®ÈÄôÂÄãËôüÁ¢ºÂéªË∑ëÂçÄÈñìÔºåÊ±∫ÂÆöÊäΩÂà∞Âì™ÂÄãË≥û
  var cumulative = 0;
  var chosenStock = null;

  for (var j = 0; j < this.box.prizeStocks.length; j++) {
    var stock = this.box.prizeStocks[j];
    if (stock.remainingQty <= 0) {
      continue;
    }
    cumulative += stock.remainingQty;
    if (ticketNo <= cumulative) {
      chosenStock = stock;
      break;
    }
  }

  if (!chosenStock) {
    alert("ÊäΩÁçéË®àÁÆóÈåØË™§ÔºåË´ãÊ™¢Êü•Â∫´Â≠òË≥áÊñô");
    return;
  }

  // 4) ÊâæÂà∞Â∞çÊáâÁöÑÁçéÈ†ÖË≥áÊñô
  var prize = this.prizeTiers.find(function (p) {
    return p.id === chosenStock.prizeTierId;
  });

  if (!prize) {
    alert("Êâæ‰∏çÂà∞Â∞çÊáâÁçéÈ†ÖË≥áÊñô");
    return;
  }

  // 5) Êõ¥Êñ∞Â∫´Â≠òËàáÁõíÂÖßÂâ©È§òÊäΩÊï∏
  chosenStock.remainingQty = chosenStock.remainingQty - 1;
  this.box.remainingTickets = this.box.remainingTickets - 1;

  // 6) Êõ¥Êñ∞Áï´Èù¢ÁãÄÊÖãÔºöÁµêÊûúË¶ñÁ™ó„ÄÅÊ∏ÖÂñÆÈ´ò‰∫Æ„ÄÅÂ∑¶ÂÅ¥ÁÄèË¶ΩÂúñÁâáÂêåÊ≠•
  this.currentPrize = prize;
  this.showResultModal = true;
  this.selectPrize(prize);
}

  }
};
</script>

